@charset "UTF-8";
*,
html {
  margin: 0;
  padding: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

ol {
  list-style-position: inside;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
  margin: 0;
}

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
  appearance: none;
}

@font-face {
  font-family: "Futura";
  src: url("../fonts/FuturaPT-Bold.woff2") format("woff2"), url("../FuturaPT-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Futura";
  src: url("../fonts/FuturaPT-Medium.woff2") format("woff2"), url("../FuturaPT-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Futura";
  src: url("../fonts/FuturaPT-Book.woff2") format("woff2"), url("../FuturaPT-Book.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

html {
  min-height: 100%;
  height: 100%;
}

body {
  position: relative;
  height: 100%;
  min-height: 100%;
  background-color: #4c3d74;
  color: #fff;
  font-family: "Futura", sans-serif, sans-serif;
  font-weight: 400;
  font-style: 18px;
}

.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.lock {
  overflow: hidden;
}

.container {
  max-width: 1190px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

@media (max-width: 991.98px) and (orientation: portrait) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media (max-width: 767.98px) and (orientation: portrait) {
  .container {
    padding-left: 57px;
    padding-right: 57px;
  }
}

@media (max-width: 412px) and (orientation: portrait) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media (max-height: 479.98px) and (max-width: 1023px) and (orientation: landscape) {
  .container {
    padding-left: 57px;
    padding-right: 57px;
  }
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px !important;
  width: 1px !important;
  padding: 0 !important;
  border: 0 !important;
  overflow: hidden;
}

button.btn-reset {
  margin: 0;
  padding: 0;
  border: none;
  background: transparent;
  color: inherit;
  font: inherit;
  cursor: pointer;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  -webkit-appearance: none;
}

button.btn-reset::-moz-focus-inner {
  border: 0;
  padding: 0;
}

ul.list-reset,
ul.list-reset li,
ul.list-reset ul li {
  margin: 0;
  padding: 0;
  text-indent: 0;
  list-style: none;
  list-style-type: none;
}

input.input-reset::-ms-clear {
  display: none;
}

.main-heading {
  font-size: 58px;
  font-weight: 700;
}

@media (max-width: 1172px) {
  .main-heading {
    font-size: 45px;
  }
}

@media (max-width: 767.98px) {
  .main-heading {
    font-size: 35px;
  }
}

@media (max-width: 412px) {
  .main-heading {
    font-size: 20px;
  }
}

.btn-link-accent {
  display: inline-block;
  padding: 18px 63px;
  border: 3px solid transparent;
  border-radius: 30px;
  background-color: #a387c4;
  color: inherit;
  font-size: 18px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

@media (min-width: 767.98px) {
  .btn-link-accent:hover {
    background-color: #4c3d74;
  }
  .btn-link-accent:focus {
    background-color: #4c3d74;
  }
}

@media (max-width: 767.98px) {
  .btn-link-accent {
    font-size: 14px;
  }
}

.btn-link-border {
  display: inline-block;
  padding: 18px 63px;
  border: 3px solid #a387c4;
  border-radius: 30px;
  background-color: #4c3d74;
  color: inherit;
  font-size: 18px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

@media (min-width: 767.98px) {
  .btn-link-border:hover {
    background-color: #a387c4;
  }
  .btn-link-border:focus {
    background-color: #a387c4;
  }
}

@media (max-width: 767.98px) {
  .btn-link-border {
    font-size: 14px;
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 105px;
  z-index: 2;
  transition: padding 0.3s ease;
}

.header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #4c3d74;
  z-index: 2;
}

.header.header--active {
  padding-top: 10px;
  padding-bottom: 10px;
}

.header__body {
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__heading {
  margin-right: 60px;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
}

.header__heading span {
  display: block;
}

@media (max-width: 767.98px) {
  .header__heading {
    font-size: 14px;
  }
}

.header__burger {
  display: none;
  flex: 0 0 auto;
}

.header__list {
  display: flex;
  flex-wrap: wrap;
}

.header__item:not(:last-child) {
  margin-right: 30px;
}

.header__link {
  color: #fff;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
}

@media (max-width: 767px) {
  .header {
    padding-top: 90px;
  }
  .header__burger {
    display: block;
    position: relative;
    width: 40px;
    height: 20px;
    z-index: 3;
    position: relative;
  }
  .header__burger span {
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #fff;
    left: 0;
    top: 9px;
    transition: all 0.3s ease 0s;
  }
  .header__burger:before, .header__burger:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #fff;
    left: 0;
    transition: all 0.3s ease 0s;
  }
  .header__burger:before {
    top: 0;
  }
  .header__burger:after {
    bottom: 0;
  }
  .header__burger.active span {
    transform: scale(0);
  }
  .header__burger.active:before {
    transform: rotate(45deg);
    top: 9px;
  }
  .header__burger.active:after {
    transform: rotate(-45deg);
    bottom: 9px;
  }
  .header__menu {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #a387c4;
    padding: 95px 57px 20px 57px;
    transition: all 0.3s ease 0s;
    overflow: auto;
  }
  .header__menu.active {
    top: 0;
  }
  .header__list {
    display: block;
  }
  .header__item {
    margin: 0 0 20px 0px;
  }
  .header__link {
    font-size: 26px;
  }
}

@media (max-width: 767px) {
  body.lock {
    overflow: hidden;
  }
}

.content {
  padding-top: 100px;
}

@media (max-width: 767px) {
  .content {
    padding-top: 70px;
  }
}

.content__text {
  font-size: 20px;
  line-height: 24px;
  text-align: justify;
}

.content__text p {
  margin-top: 20px;
}

.content__text p:first-child {
  margin-top: 0;
}

.promo {
  position: relative;
  width: 100%;
  height: 100vh;
  min-height: 800px;
}

@media (max-width: 767.98px) {
  .promo {
