    min-height: 100vh;
  }
}

.promo__container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  height: 100%;
  padding-top: 207px;
  padding-bottom: 145px;
}

@media (max-width: 767.98px) {
