}

.promo__heading {
  position: relative;
  z-index: 1;
  max-width: 767px;
  margin-bottom: 36px;
  line-height: 1.1;
}

.promo__heading span {
  display: block;
  margin-bottom: 23px;
  font-size: 23px;
  line-height: 1;
}

@media (max-width: 767.98px) {
  .promo__heading span {
    display: block;
    margin-bottom: 23px;
    font-size: 20px;
    line-height: 1;
  }
}

@media (max-width: 479.98px) {
  .promo__heading span {
    display: block;
    margin-bottom: 32px;
    font-size: 18px;
    line-height: 1;
  }
}

@media (max-width: 767.98px) {
  .promo__heading {
    text-align: center;
    margin-bottom: 32px;
  }
}

.promo__text {
  max-width: 450px;
  margin-bottom: 45px;
  font-size: 25px;
  font-weight: 500;
  line-height: 1.1;
}

@media (max-width: 767.98px) {
  .promo__text {
    max-width: 250px;
    margin: 0 auto;
    margin-bottom: 30px;
    font-size: 20px;
    text-align: center;
  }
}

@media (max-width: 412px) {
  .promo__text {
    font-size: 14px;
  }
}

@media (max-width: 767.98px) {
  .promo__links {
    align-self: stretch;
    display: flex;
    flex-direction: column;
    margin: 0 18px;
    margin-bottom: 45px;
  }
}

@media (max-width: 412px) {
  .promo__links {
    margin: 0;
    margin-bottom: 45px;
  }
}

.promo__link:not(:last-child) {
  margin-right: 18px;
}

@media (max-width: 767.98px) {
  .promo__link:not(:last-child) {
    margin-right: 0;
    margin-bottom: 20px;
  }
}

@media (max-width: 412px) {
  .promo__link {
    padding-right: 0;
    padding-left: 0;
    width: 100%;
  }
}

.promo__img {
  position: absolute;
  right: 0;
  bottom: 50%;
  width: 390px;
  height: 430px;
  object-fit: cover;
  z-index: 0;
  transform: translateY(50%);
}

@media (max-width: 1172px) {
  .promo__img {
    width: 290px;
    height: 330px;
  }
}

@media (max-width: 991.98px) {
  .promo__img {
    width: 190px;
    height: 230px;
  }
}

@media (max-width: 767.98px) {
  .promo__img {
    position: relative;
    bottom: auto;
    align-self: center;
    width: 290px;
    height: 330px;
    transform: translateY(0);
  }
}

@media (max-width: 550px) {
  .promo__img {
    position: relative;
    bottom: auto;
    align-self: center;
    width: 85%;
    height: 85%;
    padding-right: 25px;
    transform: translateY(0);
  }
}

.snackbar {
  position: fixed;
  bottom: 30px;
  right: 50px;
  z-index: 1;
  width: auto;
  min-width: 200px;
  padding: 15px;
  visibility: hidden;
  border-radius: 2px;
  color: #fff;
  text-align: center;
}

@media (max-width: 767.98px) {
  .snackbar {
    right: 50%;
    transform: translateX(50%);
  }
}

.snackbar.show-notification {
  visibility: visible;
  background-color: #333;
  /* Если 3000 в js, то в стилях 2.6s */
  animation: fadein 0.5s, fadeout 0.5s 2.6s;
}

.snackbar.show-attention {
  visibility: visible;
  background-color: #ff6060;
  /* Если 3000 в js, то в стилях 2.6s */
  animation: fadein 0.5s, fadeout 0.5s 2.6s;
}

@keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}
